import React, {useEffect, useState} from 'react'
import '../css/investor.css'
import {useWeb3React} from "@web3-react/core";
import {Web3Provider} from "@ethersproject/providers";
import {getCampaignContract, getInvestedAmount, prettyPrintBigNumber} from "../utils";
import {InvestorCheckPage} from "./investor-check";
import {InvestorBuyPage} from "./investor-buy-page";


export function InvestorPage({walletAddress, language}) {
    const { active, account, library, connector, activate, setError, deactivate } = useWeb3React<Web3Provider>()
    const [invested, setInvested] = useState<string>()
    const [showBuyPage, setShowBuyPage] = useState(false)



    useEffect(() => {
        (async () => {
            try {
                const campaignContract = await getCampaignContract(library)
                const investedBN = await getInvestedAmount(campaignContract, account)
                setInvested(investedBN.isZero() ? '' : prettyPrintBigNumber(investedBN))
            } catch (e) {
                console.error(e)
            }

            // setPublicRoundActive(await campaignContract.publicRoundActive())
        })().then()
    }, [])


    if (invested === undefined )
        return <div></div>
    else if (invested === '' || showBuyPage)
        return <InvestorBuyPage language={language} showBackButton={invested !== ''} backHandler={() => setShowBuyPage(false)}/>
    else
        return <InvestorCheckPage language={language} buyHandler={() => setShowBuyPage(true)}/>
}

























